import { _decorator, Component, Node, AudioClip } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('AudioManager')
export class AudioManager extends Component {


	@property({type: AudioClip})
 	public coinsWin: AudioClip|null = null;

 	@property({type: AudioClip})
 	public coinsInsert: AudioClip|null = null;

 	@property({type: AudioClip})
 	public jackpotWin: AudioClip|null = null;

 	@property({type: AudioClip})
 	public lineWin: AudioClip|null = null;

 	@property({type: AudioClip})
 	public reelStart: AudioClip|null = null;

 	@property({type: AudioClip})
 	public reelRoll: AudioClip|null = null;

 	@property({type: AudioClip})
 	public reelStop: AudioClip|null = null;

 	@property({type: AudioClip})
 	public gameOver: AudioClip|null = null;

 	@property({type: AudioClip})
 	public coinsInsert: AudioClip|null = null;

 	@property({type: AudioClip})
 	public coinsInsert: AudioClip|null = null;

    start() {

    }

    update(deltaTime: number) {
        
    }

    private static _instance: AudioManager;

    static get Instance() {
        if (this._instance) {
            return this._instance;
        }

        this._instance = new AudioManager();
        return this._instance;
    }

	onLoad() {
	    this._instance = this;
	}

    playCoinsWin = () => {
        cc.audioEngine.playMusic(this.coinsWin, false);
    }
    playCoinsInsert = () => {
        cc.audioEngine.playEffect(this.coinsInsert, false);
    }
    playJackpotWin = () => {
        cc.audioEngine.playEffect(this.jackpotWin, false);
    }
    playLineWin = () => {
        cc.audioEngine.playEffect(this.lineWin, false);
    }
    playReelStart = () => {
    console.log(cc.audioEngine, '----');
        cc.audioEngine.playEffect(this.reelStart, false);
    }
    playReelRoll = () => {
        this.playSound(this.reelRoll);
    }
    playReelStop = () => {
        cc.audioEngine.playEffect(this.reelStop, false);
    }
    playGameOver = () => {
        cc.audioEngine.playEffect(this.gameOver, false);
    }
    playSound = (audioClip)=>{
        //audio play
        if (!audioClip){
            return;
        }
        cc.audioEngine.playMusic(audioClip, false);
    }

    playSound (name: string, volumeScale: number = 1 ) {
        const audioSource = this.getComponent('AudioSource');

        audioSource.playOneShot(audioClip, volumeScale);

    }
    
}

